@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Sigmar+One&display=swap);

.navbar{
    background-color: #F23229;
}
.nav-container{
    display: -webkit-flex;
    display: flex; 
    -webkit-justify-content: flex-end; 
            justify-content: flex-end;
}

.dropdown-menu.show {
    color: white;
    background-color: #F23229;
}
a.dropdown-item{
    color: white;
}


.dropdown-toggle::after{ color:white; }

#basic-nav-dropdown{
    margin: 0;
    padding: 0;
    padding-left: 15px;
    padding-top: 5px;
    color: white;
}

#basic-nav-dropdown a:hover{
    background-color: #fff;
    color: black;
}

.dropdown-toogle :hover{
    background-color: #fff;
    color: black;
}

.carousel-item-area {
    display: -webkit-flex;
    display: flex;
}
.carousel-indicators button:not(.active){
    background-color: white;
    border-radius: 50%;
    height: 25px;
    width: 25px;
}
.carousel-indicators button.active{
    background-color: #F23229;
    border-radius: 50%;
    height: 25px;
    width: 25px;
}
.carousel-item-area .carousel-description{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    background-color: #0588CB;
}
.carousel-item-area .carousel-description section{
    text-align: start;
    width: 65%;
    margin: auto;
}
.carousel-item-area .carousel-description section img{
 margin-bottom: 15px;   
}
.carousel-item-area .carousel-description section .middle-text{
    font-weight: 100;
    font: 27px 'Montserrat', sans-serif !important;
    color: white;
}
.carousel-item-area .carousel-description button{
    font: 27px 'Sigmar One', sans-serif !important;
    background-color: #F28F05;
    color: white;
    font-size: 24.4px;
    width: 100%;
    border-radius: 15px;
}


.carousel-item-area .carousel-description-2{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    background-color: #F28F05;
}
.carousel-item-area .carousel-description-2 section{
    text-align: end;
    width: 65%;
    margin: auto;
}
.carousel-item-area .carousel-description-2 section p{
    font-weight: bold;
    font: 3rem 'Sigmar One', sans-serif !important;
    color: #F23229;
    text-align: end;
}
.carousel-item-area .carousel-description-2 section .middle-text{
    font: 27px 'Montserrat', sans-serif !important;
    color: white;
    text-align: end;
}
.carousel-item-area .carousel-description-2 button{
    font: 27px 'Sigmar One', sans-serif !important;
    background-color: #038C3E;
    border: 0;
    color: white;
    font-size: 24.4px;
    border-radius: 15px;
    margin-left: auto;
}



.carousel-item-area .carousel-description-3{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    background-color: #038C3E;
}
.carousel-item-area .carousel-description-3 section{
    text-align: start;
    width: 65%;
    margin: auto;
}
.carousel-item-area .carousel-description-3 section h5{
    font-weight: bold;
    font: 3rem 'Sigmar One', sans-serif !important;
    color: #F28F05;
}
.carousel-item-area .carousel-description-3 section .middle-text{
    font: 27px 'Montserrat', sans-serif !important;
    color: white;
}
.carousel-item-area .carousel-description-3 button{
    font: 27px 'Sigmar One', sans-serif !important;
    background-color: #DE4E8D;
    border: 0;
    color: white;
    font-size: 24.4px;
    border-radius: 15px;
    margin-left: auto;
}


@media only screen and (max-width: 600px) {}

.about-us{
    background-color:rgb(255, 130, 130);
    overflow-y: auto;
}

.integrant-section{
    margin: 15px;
    box-shadow: 0px 0px 17px 0px rgba(0,0,0,0.41);
    border-radius: 2px;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    background-color: rgb(252, 234, 234);
}

.integrant-section .integrant-photo{
    width: 100px;
    height: 100px;
    margin: 20px;
    box-shadow: 0px 0px 17px 0px rgba(0,0,0,0.41);
}

.integrant-section .integrant-info span{
    padding-top: 5px;
    font-weight: bold;
    color: black;
    font-size: 12px;
}

.integrant-section .integrant-info.integrant-title{
    font-size: 15px;
}


@media only screen and (max-width: 600px) {
    
    .integrant-section .integrant-photo{
        width: 50px;
        height: 50px;
        margin: 10px;
    }
}
.left-tab-menu {
    background-color:#a0a0a0;
    height: 100vh;
}

.left-tab-menu .tab-button{
    cursor: pointer;
    border-radius: 0; 
    text-align: center;
    color: white;
}

.text-container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    width: 80%;
    height: 80vh;
    margin: auto;
    background-color: rgb(240, 240, 240);
}

.text-container .text-title{
    text-align: center;
    text-indent: 0;
    padding-top: 35px;
    font-size: 20px;
    font-family: 'Sigmar One';
    height: 10%;
}

.text-container .text-body{
    width: 100%;
    height: 80%;
    word-break: break-all;
}
.text-container .pag-bar{
    height: 5%;
}

@media only screen and (max-width: 600px) {
    .left-tab-menu {
        background-color:#a0a0a0;
        height: auto;
        position: absolute;
        bottom: 0;
        width: 100%;
    }
}
* {
    margin: 0;
    padding: 0;
    outline: 0;
    box-sizing: border-box;
}

body{
    font: 400 14px 'Montserrat', sans-serif !important;
    overflow: hidden !important;
    background-color:rgb(238, 238, 238);
}

p{
    text-indent: 30px;
}

@media only screen and (max-width: 600px) {
    .desktop-only{
        display: none;
    }
}
